<template>
  <div class="wrap_tab">
    <ul class="tab_lang">
      <router-link v-for="item in nav"
                   :key="item.lang"
                   tag="li"
                   :to="item.lang"
                   :class="`item_tab item_${item.name}`">
        <a :lang="item.lang"
           class="link_tab">{{ item.text }}</a>
      </router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'info-tab',
  data() {
    return {
      nav: [
        { text: '', lang: 'ko', name: 'kr' },
        { text: 'English', lang: 'en', name: 'en' },
        { text: '', lang: 'ja', name: 'ja' },
        { text: '()', lang: 'zh-Hans', name: 'hans' },
        { text: '()', lang: 'zh-Hant', name: 'hant' },
      ],
    }
  },
}
</script>



