<template>
  <div id="kakaoFoot" role="contentinfo">
    <div class="inner_footer">
      <strong class="screen_out">{{ $t('footer.title') }}</strong>

      <h2 class="screen_out">{{ $t('footer.subtitle') }}</h2>
      <div class="info_service">
        <a :href="`/${lang}/policy/privacy`" class="link_service">{{ $t('footer.terms[0]') }}</a>
        <a
          :href="`/${lang}/policy/provision`"
          class="link_service link_service2"
        >{{ $t('footer.terms[1]') }}</a>
      </div>

      <small class="info_copy">
        <span
          class="txt_copy"
          v-for="(txt, index) in $t('footer.company.number')"
          :key="index"
        >{{ txt }}</span>
        <span class="txt_copy">{{ $t('footer.company.ceo') }}</span>
        <span class="txt_copy">{{ $t('footer.company.address') }}</span>
        <span class="txt_copy">{{ $t('footer.company.cs') }}</span>
        <span class="txt_copy">
          <a href="mailto:store@kakaofriends.com" class="link_mailto">store@kakaofriends.com</a>
        </span>
        <span class="txt_copy">
          {{ $t('footer.company.gss') }} :
          <a
            href="mailto:global@kakaofriends.com"
            class="link_mailto"
          >global@kakaofriends.com</a>
        </span>
        <span class="txt_corp">
          Copyright &copy;
          <a href="#none" class="link_copyright">KAKAO IX Corp.</a>
        </span>
      </small>
    </div>
  </div>
</template>

<script>
export default {
  name: "info-footer",
  computed: {
    lang() {
      return this.$route.params.lang === "ko" ? "kr" : "en";
    }
  }
};
</script>



